* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  font-weight: 400;
}
.container {
  max-width: 1350px;
  width: 100%;
  margin: 0 auto;
}

section {
  margin-top: 4%;
}

img {
  width: 100%;
}

.row {
  display: flex;
  flex-direction: row;
}

.flex-wrap {
  flex-wrap: wrap;
}

ul {
  li {
    list-style-type: none;
  }
}
select,
input[type="text"] {
  font-family: "Montserrat", sans-serif;
  padding: 0.5rem;
  outline: none;
  border-radius: 0.5rem;
  border: 1px solid $color-dark;
  width: 200px;
}
a {
  text-decoration: none;
  cursor: pointer;
  color: $color-dark;
  font-weight: 600;
  &:hover {
    color: $color-primary;
  }
}
.pointer {
  cursor: pointer;
}
button {
  cursor: pointer;
}
.button-text {
  color: $color-light;
  &:hover {
    color: $color-light;
  }
}
.align-item-center {
  align-items: center !important;
}
.space-between {
  justify-content: space-between !important;
}
.space-around {
  justify-content: space-around !important;
}

.space-evenly {
  justify-content: space-evenly !important;
}

.justify-center {
  justify-content: center !important;
}
.position-relative {
  position: relative;
}
.primary-color {
