::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: $color-primary;
}
::-webkit-scrollbar-track {
  background-color: $color-shadow;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}
